const _import = require('./_import_' + process.env.NODE_ENV);

//login 
const Login = _import('Login/Login');

//layout
const layout = _import('main');

//
const main = _import('index/index');

/**  start **/
//
const userMain = _import('user/user');
//
const addUser = _import('user/add');
//
const editUser = _import('user/edit');

//
const changPwd = _import('user/pwd');
/**  end */

/**  start */

//
const authMain = _import('auth/rule');
//
const addAuth = _import('auth/add');
//
const editAuth = _import('auth/edit');
/** end */

/**   start  **/

//
const brand = _import('brand/index');
//
const brandAdd = _import('brand/add');
//
const brandEdit = _import('brand/edit');
//
const cate = _import('cate/index');

//
const goodsTempMain = _import('attr/index');
//
const addGoodsTemp = _import('attr/add');
// 
const editGoodsTemp = _import('attr/edit');

//
const goodsMain = _import('goods/index');
//
const addGoods = _import('goods/add');
//
const editGoods = _import('goods/edit');
/**  end  **/


/**  start **/
const seller = _import('seller/index');
//
const sellerAdd = _import('seller/add');
//
const sellerEdit = _import('seller/edit');
//
const sellerInfo = _import('seller/info');
//
const sellerAsslist = _import('asslist/index');

/** end **/

/**  start **/
//
const consumer = _import('consumer/index');
//
const consumerInfo = _import('consumer/info');
/** end **/

/**  start **/

//
const order = _import('orders/index');
//
const orderInfo = _import('orders/info');

//
const call = _import('orders/call');
//
const buy = _import('orders/buy');

/**  end **/

//**  start */
const finance = _import('finance/index');
//
const cash = _import('finance/cash');
//
const refund = _import('finance/refund');
//
const refundInfo = _import('finance/refundInfo');

//**  end */

/**  start **/
//
const godown = _import('godown/index');

//
const godown_create = _import('godown/add');
//
const godown_update = _import('godown/edit');
//
const godown_user = _import('godown/user');
//
const godown_adduser = _import('godown/adduser');

const postList = _import('post/index')
const postDetail = _import("post/detail")
const replyDetail=_import("post/replyDetail")

/**  end **/

export default [
  {
    path: '/login', component: Login, hidden: true
  },
  {
    path: '/', redirect: '/main', hidden: true
  },
  {
    path: '/main', name: '', icon: 'grid', component: layout, noDropdown: true, children: [{ path: '', name: '', component: main }]
  },
  {
    path: '/post', name: '', icon: 'ios-cog', component: layout, redirect: '/post/postList',
    children: [
      { path: 'list', name: '', meta: { alias: 'postList' }, component: postList },
      { path: 'detail', name: '', meta: { alias: 'postDetail' }, component: postDetail ,hidden: true  },
      { path: 'replyDetail', name: '', meta: { alias: 'replyDetail' }, component: replyDetail ,hidden: true  }

      // menu
    ]
  },
  {
    path: '/sys', name: '', icon: 'ios-cog', component: layout, redirect: '/sys/user',
    children: [
      { path: 'user', name: '', meta: { alias: 'user' }, component: userMain },
      { path: 'user/add', name: '', meta: { alias: 'user_add' }, component: addUser, hidden: true },
      { path: 'user/edit', name: '', meta: { alias: 'user_edit' }, component: editUser, hidden: true },
      { path: 'user/change', name: '', meta: { alias: 'user_pwd' }, component: changPwd, hidden: true },
      { path: 'group', name: '', meta: { alias: 'group' }, component: authMain },
      { path: 'group/add', name: '', meta: { alias: 'group_add' }, component: addAuth, hidden: true },
      { path: 'group/edit', name: '', meta: { alias: 'group_edit' }, component: editAuth, hidden: true }
    ]
  },
  {
    path: '/pro', name: '', component: layout, redirect: '/pro/goods', icon: 'android-playstore',
    children: [
      { path: 'brand', name: '', meta: { alias: 'brand' }, component: brand },
      { path: 'brand/add', name: '', meta: { alias: 'brand_create' }, component: brandAdd, hidden: true },
      { path: 'brand/edit', name: '', meta: { alias: 'brand_update' }, component: brandEdit, hidden: true },
      { path: 'cate', name: '', meta: { alias: 'cate' }, component: cate },
      { path: 'cate/add', name: '', meta: { alias: 'cate_create' }, component: addGoodsTemp, hidden: true },
      { path: 'cate/edit', name: '', meta: { alias: 'cate_update' }, component: editGoodsTemp, hidden: true },
      { path: 'attrtemp', name: '', meta: { alias: 'attr' }, component: goodsTempMain },
      { path: 'attrtemp/add', name: '', meta: { alias: 'attr_create' }, component: addGoodsTemp, hidden: true },
      { path: 'attrtemp/edit', name: '', meta: { alias: 'attr_update' }, component: editGoodsTemp, hidden: true },
      { path: 'goods', name: '', meta: { alias: 'goods' }, component: goodsMain },
      { path: 'goods/add', name: '', meta: { alias: 'goods_add' }, component: addGoods, hidden: true },
      { path: 'goods/edit', name: '', meta: { alias: 'godos_edit' }, component: editGoods, hidden: true },
    ]
  },
  {
    path: '/seller', name: '', component: layout, redirect: '/seller/store', icon: 'pricetags',
    children: [
      { path: 'store', name: '', meta: { alias: 'seller' }, component: seller },
      { path: 'store/add', name: '', meta: { alias: 'seller_create' }, component: sellerAdd, hidden: true },
      { path: 'store/edit', name: '', meta: { alias: 'seller_update' }, component: sellerEdit, hidden: true },
      { path: 'store/info', name: '', meta: { alias: 'seller_info' }, component: sellerInfo, hidden: true },
      { path: 'asslist', name: '', meta: { alias: 'seller_all_asslist' }, component: sellerAsslist }
    ]
  },
  {
    path: '/consumer', name: '', component: layout, redirect: '/consumer/index', icon: 'cube',
    children: [
      { path: 'index', name: '', meta: { alias: 'consumer' }, component: consumer },
      { path: 'info', name: '', meta: { alias: 'consumer_info' }, component: consumerInfo, hidden: true },
    ]
  },
  {
    path: '/order', name: '', component: layout, redirect: '/order/index', icon: 'bag',
    children: [
      { path: 'index', name: '', meta: { alias: 'order' }, component: order },
      { path: 'info', name: '', meta: { alias: 'order_info' }, component: orderInfo, hidden: true },
      { path: 'call', name: '', meta: { alias: 'consumer_call' }, component: call },
      { path: 'buy', name: '', meta: { alias: 'order_create' }, component: buy },
    ]
  },
  {
    path: '/finance', name: '', component: layout, redirect: '/finance/index', icon: 'social-yen',
    children: [
      { path: 'index', name: '', meta: { alias: 'finance_pay' }, component: finance },
      { path: 'pay', name: '', meta: { alias: 'finance_pay' }, component: finance, hidden: true },
      { path: 'urefund', name: '', meta: { alias: 'finance_refund' }, component: finance, hidden: true },
      { path: 'purlist', name: '', meta: { alias: 'finance_purlist' }, component: finance, hidden: true },
      { path: 'settlement', name: '', meta: { alias: 'finance_settle' }, component: finance, hidden: true },
      { path: 'approvallist', name: '', meta: { alias: 'finance_cash' }, component: cash },
      { path: 'approval', name: '', meta: { alias: 'finance_cash_audit' }, component: finance, hidden: true },
      { path: 'refundlist', name: '', meta: { alias: 'finance_refund_applf' }, component: refund },
      { path: 'refundinfo', name: '', meta: { alias: 'finance_refund_audit' }, component: refundInfo, hidden: true },
    ]
  },
  {
    path: '/godown', name: '', component: layout, redirect: '/godown/index', icon: 'soup-can',
    children: [
      { path: 'index', name: '', meta: { alias: 'godown' }, component: godown },
      { path: 'add', name: '', meta: { alias: 'godown_create' }, component: godown_create, hidden: true },
      { path: 'edit', name: '', meta: { alias: 'godown_update' }, component: godown_update, hidden: true },
      { path: 'user', name: '', meta: { alias: 'godown_userlist' }, component: godown_user },
      { path: 'useradd', name: '', meta: { alias: 'godown_adduser' }, component: godown_adduser, hidden: true },
    ]
  }


]


